import "../scss/main.scss";

const togglers = document.querySelectorAll("[data-modal]");

if (togglers.length > 0) {
  togglers.forEach((t) => {
    const targetModalId = t.getAttribute("data-modal");

    if (targetModalId !== "") {
      const targetModal = modal(targetModalId);
      const dismissBtn = targetModal.querySelector(".m-modal-dismiss");

      if (dismissBtn)
        dismissBtn.addEventListener("click", () => targetModal.dismiss());

      targetModal.addEventListener("click", function (e) {
        if (e.target === targetModal) targetModal.dismiss();
      });

      t.addEventListener("click", function () {
        targetModal.open();
      });
    }
  });
}

/**
 * finds and returns the modal element by the given string modalId parameter.
 * @param {string} modalId
 * @returns {Element}
 */

function modal(modalId) {
  const modal = document.querySelector("#" + modalId);
  modal.open = open;
  modal.dismiss = dismiss;

  return modal;
}

function open() {
  this.classList.add("open");
}

function dismiss() {
  this.classList.remove("open");
}

export { modal };
